html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Verdana";
  font-style: normal;
  font-weight: 400;
  src: local("Verdana"), local("Verdana"), url("../fonts/Verdana.woff2") format("woff2"), url("../fonts/Verdana.woff") format("woff");
}

@font-face {
  font-family: "Verdana";
  font-style: italic;
  font-weight: 400;
  src: local("Verdana-Italic"), local("Verdana-Italic"), url("../fonts/Verdana-Italic.woff2") format("woff2"), url("../fonts/Verdana-Italic.woff") format("woff");
}

@font-face {
  font-family: "Verdana";
  font-style: normal;
  font-weight: 700;
  src: local("Verdana-Bold"), local("Verdana-Bold"), url("../fonts/Verdana-Bold.woff2") format("woff2"), url("../fonts/Verdana-Bold.woff") format("woff");
}

@font-face {
  font-family: "Verdana";
  font-style: italic;
  font-weight: 700;
  src: local("Verdana-BoldItalic"), local("Verdana-BoldItalic"), url("../fonts/Verdana-BoldItalic.woff2") format("woff2"), url("../fonts/Verdana-BoldItalic.woff") format("woff");
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #fff;
  line-height: 1.86;
  letter-spacing: 0.063em;
  font-family: 'Verdana', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-weight: 400;
  font-size: 15px;
  color: #383838;
  min-width: 320px;
  margin: 0;
  padding: 0;
  border: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: currentColor;
}

a:hover {
  text-decoration: none;
}

.inner {
  max-width: 1360px;
  margin: 0 auto;
  padding: 0 30px;
}
